@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.4/css/bulma.min.css);
html{font-family:Arial,"Helvetica Neue",Helvetica,sans-serif}body{padding:20px}label,button{cursor:pointer}.error{color:red}.TodoInfo{width:-webkit-max-content;width:max-content;padding:8px;margin:12px 0;border:1px solid #000;border-radius:8px;background-color:#faebd7}.TodoInfo__title{margin:4px 0;font-size:inherit;color:red}.TodoInfo--completed .TodoInfo__title{color:#080}.UserInfo{font-style:italic}
